function sliderPartners () {
    var swiper = new Swiper('.partners .swiper', {
        slidesPerView: "5",
        spaceBetween: 45,
        navigation: {
            nextEl: '.partners .swiper-button-next',
            prevEl: '.partners .swiper-button-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: 2,
                spaceBetween: 15,
            },
            576: {
                slidesPerView: 3,
                spaceBetween: 15,
            },
            992: {
                slidesPerView: 4,
                spaceBetween: 15,
            },
            1201: {
                slidesPerView: "5",
                spaceBetween: 45,
            }
        }
    })
}

$(document).ready(function() {

    sliderPartners();

    // Burger
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header-bottom').toggleClass('header-bottom--active')
        $('body').toggleClass('fixed-body')
    })

    // Select
    $('.select__list-default').click(function () {
        $(this).parent('.select__wrap').addClass('select__wrap--active')
    })

    $('.select__list p').click(function () {
        var currentele = $(this).html()
        $(this).parents('.select__wrap').find('.select__current').html(currentele)
        $(this).parents('.select__wrap').removeClass('select__wrap--active')
        $(this).siblings(".select__list p").removeClass("active")
        $(this).addClass("active")
        $(this).parents('.select__wrap').find('.select__current').addClass('select__current--active')
    })

    let selectWrap = $('.select__wrap')

    $(document).mouseover(function (e) {
        if ( !selectWrap.is(e.target) && selectWrap.has(e.target).length === 0

        ) {
            selectWrap.removeClass('select__wrap--active')
        }
    })

    // Date
    new AirDatepicker('#date', {
        range: true,
        multipleDatesSeparator: ' - ',
    });

})